import React from 'react';
//import useMediaQuery from '@material-ui/core/useMediaQuery';
import { ThemeProvider,createMuiTheme } from '@material-ui/core/styles';
import CssBaseline from '@material-ui/core/CssBaseline';
//import logo from './logo.svg';
//import './App.css';
//import NavBar from './main/NavBar';
import Index from './main/Index_';
function App() {
  const [thememode,setThememode] = React.useState("light");
  const [appBarColor,setAppBarColor] = React.useState("#1976d2");
  const [fabColor,setFabColor] = React.useState("#e91e63");

  const themeChange = () => {
  if(thememode === "light"){
    setThememode("dark");
    setAppBarColor("#303030");
    setFabColor("#ff80ab");
  }else{
    setThememode("light");
    setAppBarColor("#1976d2");
    setFabColor("#e91e63");
  }
}
  //const prefersDarkMode = useMediaQuery('(prefers-color-scheme: dark)');
  const theme = createMuiTheme({overrides:{MuiAppBar:{colorPrimary:{backgroundColor:appBarColor}},MuiFab:{secondary:{backgroundColor:fabColor}}},palette: {type:thememode,primary:{main:"#1976d2"}}});
 //console.log(theme);
  return (
    <ThemeProvider theme={theme}>
    <React.Fragment>
<CssBaseline />
      <Index themes={themeChange}/>
    </React.Fragment>
</ThemeProvider>
  );
}

export default App;