import React,{Component,useRef}from 'react';
//import Button from '@material-ui/core/Button';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import PrintIcon from '@material-ui/icons/Print';
import {IconButton} from '@material-ui/core';
import ReactToPrint from 'react-to-print';
import WidgetsIcon from '@material-ui/icons/Widgets';
import AttachMoneyIcon from '@material-ui/icons/AttachMoney';
import TimelineIcon from '@material-ui/icons/Timeline';
import List from '@material-ui/core/List';
import ListItem from '@material-ui/core/ListItem';
import ListItemIcon from '@material-ui/core/ListItemIcon';
import ListItemText from '@material-ui/core/ListItemText';
import {Paper,AppBar,Toolbar,Fab} from '@material-ui/core';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';

export default function PrintModal(props) {
  const componentRef = useRef();
  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

const totalAmount = (zz) => {
  let value= 0;
  for(const x in props.stt.DAta.milk){

    if(props.stt.DAta.milk[x].dateTime.DATE.value <= props.stt.tfiltertod && props.stt.DAta.milk[x].dateTime.DATE.value >= props.stt.tfilterfrd && props.stt.DAta.milk[x].dateTime.MONTH.value === props.stt.tfiltermod && props.stt.DAta.milk[x].dateTime.YEAR.value === props.stt.tfilteryed){
      value+= props.stt.DAta.milk[x][zz];

  }
  }
     return value;
  }
const totalTimes = () => {

    let totalTime= 0;
  for(const x in props.stt.DAta.milk){

    if(props.stt.DAta.milk[x].dateTime.DATE.value <= props.stt.tfiltertod && props.stt.DAta.milk[x].dateTime.DATE.value >= props.stt.tfilterfrd && props.stt.DAta.milk[x].dateTime.MONTH.value === props.stt.tfiltermod && props.stt.DAta.milk[x].dateTime.YEAR.value === props.stt.tfilteryed){
      totalTime++;
  }
  }
     return totalTime;
}

  return (
    <div>
      <IconButton  color="inherit" onClick={handleClickOpen}>
        <PrintIcon />
      </IconButton>
      <Dialog
        fullScreen={true}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle component={'span'} style={{padding:"0px",marginBottom:"50px"}} id="responsive-dialog-title"><AppBar color="primary" position="fixed" elevation={2} ><Toolbar><IconButton onClick={handleClose} color="inherit"><ArrowBackIcon /></IconButton><ReactToPrint
          trigger={() => <Fab color="secondary" style={{position:"absolute",bottom:-20,right:0,left:0,zIndex:1,margin:"0 auto"}} aria-label="edit"><PrintIcon /></Fab>}
          content={() => componentRef.current}
        /></Toolbar></AppBar></DialogTitle>
        <DialogContent style={{padding:"10px 0 0 0"}}>
          <DialogContentText component={'span'} >
  <ComponentToPrint dataa={totalAmount} toDate={props.stt.tfiltertod} frDate={props.stt.tfilterfrd} ofMonth={props.stt.tfiltermod} ofYear={props.stt.tfilteryed} totimes={totalTimes} ref={componentRef} />
          </DialogContentText>
        </DialogContent>
        <DialogActions>

        </DialogActions>
      </Dialog>
    </div>
  );
}



class ComponentToPrint extends Component{
  render(){
  return (
      <Paper elevation={2} style={{border:"0.2px solid rgba(0,0,0,0.3)",margin:"1rem 12px"}}>
      <List>
    <ListItem>
    <ListItemText style={{color:"grey"}}>
 &nbsp; From : &nbsp;{this.props.frDate}-{this.props.ofMonth}-{this.props.ofYear}&nbsp; &nbsp;||&nbsp; &nbsp; TO : &nbsp; {this.props.toDate}-{this.props.ofMonth}-{this.props.ofYear}
    </ListItemText>
    </ListItem>
                <ListItem>
        <ListItemIcon>
          <WidgetsIcon />
        </ListItemIcon>
        <ListItemText style={{color:"grey"}}>
      {this.props.dataa("Qty")}
        </ListItemText>
      </ListItem>
      <ListItem >
        <ListItemIcon>
          <AttachMoneyIcon />
        </ListItemIcon>
        <ListItemText style={{color:"grey"}}>
        {this.props.dataa("Amount")}
        </ListItemText>
      </ListItem>
      <ListItem>
        <ListItemIcon>
          <TimelineIcon />
        </ListItemIcon>
        <ListItemText style={{color:"grey"}} primary={this.props.totimes()}/>

      </ListItem>
     </List>
</Paper>
    );
  }
}