import React from 'react';
import {Button,IconButton} from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import useMediaQuery from '@material-ui/core/useMediaQuery';
import { useTheme } from '@material-ui/core/styles';
import Slider from '@material-ui/core/Slider';
import Typography from '@material-ui/core/Typography';
import { FiFilter } from "react-icons/fi";

export default function TotalFilter(props) {
  const [open, setOpen] = React.useState(false);
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const datea = new Date();
  const damonth = datea.getMonth()+1;
  const dayear = datea.getFullYear();
const data = {todate:31,fromdate:1,month:damonth,year:dayear};
  const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };
  const fromdatehandler = (event,value) =>{
    data.fromdate = value;

  }
  const todatehandler = (event,value) =>{
    data.todate = value;
  }
  const monthhandler = (event,value) =>{
    data.month = value;
  }
  const yearhandler = (event,value) =>{
    data.year = value;
  }
  const submitHandler = () => {

    if(!('todate' in data)){
      data.todate = 31;
    }
    if(!('fromdate' in data)){
      data.fromdate = 1;
    }
    if(!('month' in data)){
      data.month = damonth;
    }
    if(!('year' in data)){
      data.year = dayear;
    }

    props.filter(data);
    handleClose();
  }
 const datemarks =[
   {value:1,label:1},
   {value:5,label:5},
   {value:10,label:10},
   {value:15,label:15},
   {value:20,label:20},
   {value:25,label:25},
   {value:31,label:31},

 ];
  const monthmarks =[
   {value:1,label:1},
   {value:2,label:2},
   {value:3,label:3},
   {value:4,label:4},
   {value:5,label:5},
   {value:6,label:6},
   {value:7,label:7},
   {value:8,label:8},
   {value:9,label:9},
   {value:10,label:10},
   {value:11,label:11},
   {value:12,label:12}
 ];
const yearmarks =[
{value:2020,label:2020},
   {value:2021,label:2021},
   {value:2022,label:2022},
   {value:2023,label:2023},
   {value:2024,label:2024},
  {value:2025,label:2025}
];
  return (
    <div>
      <IconButton variant="outlined" edge="start" color="inherit" onClick={handleClickOpen}>

      <FiFilter />

      </IconButton>
      <Dialog
         fullWidth
        fullScreen={fullScreen}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle id="responsive-dialog-title">Select Filters</DialogTitle>
        <DialogContent>
          <DialogContentText component={'span'}>

          <Typography id="from-date-selecter">From Date</Typography>
			<Slider min={1} max={31} step={1} defaultValue={1} valueLabelDisplay="auto" marks={datemarks} name="from-date-selector" onChange={fromdatehandler} aria-labelledby="from-date-selecter" />

            <Typography id="to-date-selecter">To Date</Typography>
		  <Slider min={1} max={31} step={1} defaultValue={31} valueLabelDisplay="auto" marks={datemarks} name="to-date-selector" onChange={todatehandler} aria-labelledby="to-date-selecter" />

               <Typography id="month-selecter">Month</Typography>
			<Slider min={1} max={12} step={1} defaultValue={1} valueLabelDisplay="auto" marks={monthmarks} name="month-selector" onChange={monthhandler} aria-labelledby="month-selecter" />

              <Typography id="month-selecter">Year</Typography>
			<Slider min={2020} max={2025} step={1} defaultValue={2020} valueLabelDisplay="auto" marks={yearmarks} name="year-selector" onChange={yearhandler} aria-labelledby="year-selecter" />


          </DialogContentText>
        </DialogContent>
        <DialogActions>
          <Button autoFocus onClick={handleClose} color="primary">
            Cancel
          </Button>
          <Button onClick={submitHandler} color="primary" autoFocus>
            Apply
          </Button>
        </DialogActions>
      </Dialog>
    </div>
  );
}