import React from 'react';
import {IconButton,AppBar,Toolbar} from '@material-ui/core';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import DialogContentText from '@material-ui/core/DialogContentText';
import DialogTitle from '@material-ui/core/DialogTitle';
import VisibilityIcon from '@material-ui/icons/Visibility';
import ArrowBackIcon from '@material-ui/icons/ArrowBack';


export default function ResponsiveDialog(props) {
  const [open, setOpen] = React.useState(false);
    const handleClickOpen = () => {
    setOpen(true);
  };

  const handleClose = () => {
    setOpen(false);
  };

  return (
    <div>
     <IconButton onClick={handleClickOpen} color="secondary">
    <VisibilityIcon />
    </IconButton>
      <Dialog
        fullScreen={true}
        open={open}
        onClose={handleClose}
        aria-labelledby="responsive-dialog-title"
      >
        <DialogTitle style={{padding:"0px",marginBottom:"70px"}}id="responsive-dialog-title"><AppBar color="primary" elevation={2} position="fixed" ><Toolbar><IconButton onClick={handleClose} color="inherit"><ArrowBackIcon /></IconButton> &nbsp; Date : {props.data[props.miid].date}</Toolbar></AppBar></DialogTitle>
        <DialogContent>
          <DialogContentText component={'span'}>
 			 {props.children}
          </DialogContentText>
        </DialogContent>
        <DialogActions>

        </DialogActions>
      </Dialog>
    </div>
  );
}