import React, {Fragment,useState} from 'react';
import { makeStyles } from '@material-ui/core/styles';
import TextField from '@material-ui/core/TextField';
import { MuiPickersUtilsProvider } from '@material-ui/pickers';
import MomentUtils from '@date-io/moment';
import { TimePicker } from "@material-ui/pickers";
import {InputLabel,Input,FormControl,Select,Button} from '@material-ui/core';
import { DatePicker } from "@material-ui/pickers";

const useStyles = makeStyles(theme => ({
  root: {
    '& > *': {
      margin: theme.spacing(1),
      width: 200,
    },
  },
}));

export default function AddNew(props) {
  const classes = useStyles();
  const [selectedDatee, handleeDateChange] = useState(new Date());
  const [selectedDate, handleDateChange] = useState(new Date());

const [shift,setShift] = React.useState('');
const handleshiftchange = (event) => {
  setShift(event.target.value);
  console.log(shift)
  //milkdata[event.target.name] = event.target.value;
};
const [rc_no_ ,setrc_no] = React.useState("");
const [currnt ,setcurrnt] = React.useState("");
const timehandler = (event) => {
  handleDateChange(event);
const timeobj={
  target:{
    name:'time',
    value:event.format('hh:mm A')
  }
}


  props.rootdata(timeobj)
}
const datehandler = (event) => {
  handleeDateChange(event);
  const dateobj={
    target:{
      name:'date',
      value:event.format('DD-MM-YYYY')
    }
  }
  props.rootdata(dateobj);
  const Dateobj={
    target:{
      name:'DATE',
      value:Number(event.format('DD'))
    }
  }
  props.rootdata(Dateobj);
const Monthobj={
    target:{
      name:'MONTH',
      value:Number(event.format('MM'))
    }
  }
  props.rootdata(Monthobj);
const Yearobj={
    target:{
      name:'YEAR',
      value:Number(event.format('YYYY'))
    }
  }
  props.rootdata(Yearobj);
}
const amountHandler = (event) => {
  props.rootdata(event);
  const totalAmount = props.stt.Qty.value * event.target.value;
  const amountobj={
    target:{
      name:'Amount',
      value:totalAmount
    }
  }
  props.rootdata(amountobj);

}
const submitHandler = (event) => {
  const st = props.stt;
  const stid = st.ID.value;
  const saveData = {[stid]:{
      dateTime:{
        DATE:{value:st.DATE.value},
        MONTH:{value:st.MONTH.value},
        YEAR:{value:st.YEAR.value}
      },
      rc_no:st.rc_no.value,
      line2:st.line2.value,
      line3:st.line3.value,
      date:st.date.value,
      time:st.time.value,
      shift:st.shift.value,
      code:st.code.value,
      MilkType:st.MilkType.value,
      Collection:st.Collection.value,
      Fat:st.Fat.value,
      CLR:st.CLR.value,
      SNF:st.SNF.value,
      Qty:Number(st.Qty.value),
      Rate:Number(st.Rate.value),
      Amount:st.Amount.value
  }
  }
  props.Data1(saveData);
  props.modalclose();
event.preventDefault();

};

  return (
    <form className={classes.root} onSubmit={submitHandler} noValidate id="addnewform" autoComplete="off">
           <TextField style={{width:"90%",marginLeft:"5%"}} name="ID" disabled onChange={props.rootdata} value={props.stt.ID.value} fullWidth required size="small" id="id" label="ID." variant="outlined" />

           <TextField style={{width:"90%",marginLeft:"5%"}} name="rc_no" onChange={props.rootdata} value={props.stt.rc_no.value} fullWidth required size="small" id="input1" label="Receipt No ." variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} name="line2" onChange={props.rootdata} value={props.stt.line2.value} fullWidth required size="small" id="input2" label="Line 2" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} name="line3" onChange={props.rootdata} value={props.stt.line3.value}fullWidth required size="small" id="input3" label="Line 3" variant="outlined" />
           <MuiPickersUtilsProvider utils={MomentUtils}>
             <Fragment>
             <DatePicker disableFuture openTo="date" style={{width:"90%",marginLeft:"5%"}} format="DD-MM-YYYY" label="Date" views={["year", "month", "date"]} value={selectedDatee} onChange={datehandler} />
           <TimePicker autoOk label="Time" name="time" style={{width:"90%",marginLeft:"5%"}} value={selectedDate} onChange={timehandler} />
           </Fragment>
           </MuiPickersUtilsProvider>
           <FormControl variant="filled" style={{width:"90%",marginLeft:"5%"}}>
             <InputLabel htmlFor="shift-select">Shift</InputLabel>
			 <Select native id="shift-select" name="shift" onChange={props.rootdata} value={props.stt.shift.value}>
               <option> </option>
               <option>Morning</option>
				<option>Night</option>
              </Select>
            </FormControl>
           <TextField style={{width:"90%",marginLeft:"5%"}} name="code" disabled value={props.stt.code.value} fullWidth required size="small" id="input7" label="Code" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} name="MilkType" onChange={props.rootdata} value={props.stt.MilkType.value} fullWidth required size="small" id="input11" label="MilkType" variant="outlined" />

           <TextField style={{width:"90%",marginLeft:"5%"}} name="Collection" onChange={props.rootdata} value={props.stt.Collection.value} fullWidth required size="small" id="input8" label="Collection" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} type="number" name="Fat" onChange={props.rootdata} value={props.stt.Fat.value} fullWidth required size="small" id="input9" label="Fat" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} type="number" name="CLR" onChange={props.rootdata} value={props.stt.CLR.value} fullWidth required size="small" id="input10" label="CLR" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} type="number" name="SNF" onChange={props.rootdata} value={props.stt.SNF.value} fullWidth required size="small" id="input11" label="SNF" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} type="number" name="Qty" onChange={props.rootdata} value={props.stt.Qty.value} fullWidth required size="small" id="input12" label="Qty" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} type="number" name="Rate" onChange={amountHandler} value={props.stt.Rate.value} fullWidth required size="small" id="input11" label="Rate" variant="outlined" />
           <TextField style={{width:"90%",marginLeft:"5%"}} type="number" name="Amount" value={props.stt.Amount.value} fullWidth required size="small" id="input12" label="Amount" variant="outlined" />
             <FormControl variant={'outlined'}>
             <Button type="submit" style={{width:"95%",marginLeft:"5%"}} variant={'contained'} value="Submit" form="addnewform" name="submit" color="primary" >Submit </Button>
            </FormControl>
               </form>
  );
}